import Placeholder from './Placeholder.vue';
import VueScrollTo from 'vue-scrollto';
import VTooltip from 'v-tooltip';
import Vue from 'vue';
Vue.use(VTooltip);
VTooltip.options.autoHide = false;
export default {
  name: 'Provider',
  components: {
    Placeholder: Placeholder
  },
  props: {
    provider: {
      type: Object,
      "default": function _default() {}
    },
    show: {
      type: Boolean,
      "default": true
    },
    init: {
      type: Boolean,
      "default": true
    },
    l10n: {
      type: Object,
      "default": function _default() {
        return {};
      }
    },
    officialApps: {
      type: Object,
      "default": function _default() {
        return {};
      }
    },
    coreApps: {
      type: Array,
      "default": function _default() {
        return [];
      }
    }
  },
  computed: {
    apps: function apps() {
      var _this = this;

      return this.provider.apps.slice(0).filter(function (app) {
        return Object.prototype.hasOwnProperty.call(_this.officialApps, app);
      }).sort(function (a, b) {
        return a.localeCompare(b);
      });
    },
    distance: function distance() {
      // Don't display distance bigger than 3000km
      if (this.locations[0].score <= 3000) {
        // rounding to the hundred
        return '< ' + Math.round(this.locations[0].score / 100) * 100 + 'km';
      } else {
        return this.l10n.far;
      }
    },
    locations: function locations() {
      return this.provider.locations.slice().sort(function (a, b) {
        return a.score - b.score;
      });
    },
    city: function city() {
      return this.locations[0].city;
    },
    selected: {
      get: function get() {
        return this.$parent.selected;
      },
